@font-face {
  font-family: 'oimaket';
  src: url('../assets/icon/oimaket.eot');
  src: url('../assets/icon/oimaket.eot?b5mcrx#iefix')
      format('embedded-opentype'),
    url('../assets/icon/oimaket.ttf?b5mcrx') format('truetype'),
    url('../assets/icon/oimaket.woff?b5mcrx') format('woff'),
    url('../assets/icon/oimaket.svg?b5mcrx#oimaket') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'oimaket' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

